.App {
  text-align: center;

  .App-logo {
    height: 40vmin;
  }

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    a{
      text-decoration: none;
      color: #c156c5 !important;
    }
  }

  .App-link {
    color: #c156c5;
  }
  
  .App-footer{
    padding: 25px;
  }
}